<template>
  <div class="register">
    <div class="register-title">
      <p>注册</p>
    </div>

    <el-form :model="registerForm" :rules="rules" class="register-form">
      <el-form-item prop="account" class="form-item-account">
        <el-input placeholder="账号" clearable v-model="registerForm.account">
          <template #prefix><i class="el-icon-user"></i></template>
        </el-input>
      </el-form-item>

      <el-form-item prop="username" class="form-item-username">
        <el-input placeholder="用户名" clearable v-model="registerForm.username">
          <template #prefix><i class="el-icon-s-custom"></i></template>
        </el-input>
      </el-form-item>

      <el-form-item prop="password" class="form-item-password">
        <el-input placeholder="密码" show-password clearable v-model="registerForm.password"
                  @keyup.enter.native="register">
          <template #prefix>
            <img src="~assets/img/login-password.png" class="icon-password">
          </template>
        </el-input>
      </el-form-item>

      <el-form-item prop="confirmPassword" class="form-item-password">
        <el-input placeholder="确认密码" show-password clearable v-model="registerForm.confirmPassword"
                  @keyup.enter.native="register">
          <template #prefix>
            <img src="~assets/img/login-password.png" class="icon-password">
          </template>
        </el-input>
      </el-form-item>

      <!--      <el-form-item class="form-item-role">-->
      <!--        <el-input placeholder="" class="role-input">-->
      <!--          <template #prepend>角色</template>-->
      <!--          <template #append>-->
      <!--            <el-radio v-model="registerForm.roleId" :label="2">普通用户</el-radio>-->
      <!--            <el-radio v-model="registerForm.roleId" :label="1">管理员</el-radio>-->
      <!--          </template>-->
      <!--        </el-input>-->
      <!--      </el-form-item>-->

      <el-form-item>
        <el-button class="button-register" :disabled="registerDisabled" @click="register">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {requestRegister} from "@/network/api"

export default {
  name: "LoginView",
  data() {
    return {
      registerDisabled: true,
      registerForm: {
        account: '',
        username: "",
        password: "",
        confirmPassword: "",
        roleId: 2
      },
      rules: {
        account:
          [
            {required: true, message: '请输入账号', trigger: 'blur'},
            {min: 2, max: 15, message: '长度在2到15个字符', trigger: 'blur'}
          ],
        username:
          [
            {required: true, message: '请输入用户名', trigger: 'blur'},
            {min: 2, max: 15, message: '长度在2到15个字符', trigger: 'blur'}
          ],
        password:
          [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {min: 6, max: 20, message: '长度在8到20个字符', trigger: 'blur'}
          ],
        confirmPassword:
          [
            {min: 8, max: 20, message: '长度在 8 到 20 个字符', trigger: 'blur'},
            {
              required: true,
              validator: (rule, value, callback) => {
                if (value === '') {
                  callback(new Error('请再次输入密码'));
                } else if (value !== this.registerForm.password) {
                  callback(new Error('两次输入密码不一致!'));
                } else {
                  this.registerDisabled = false;
                  callback();
                }
              },
              trigger: 'blur'
            }
          ]
      }
    }
  }
  ,
  methods: {
    register() {
      const registerFormData = new FormData();
      registerFormData.append("account", this.registerForm.account)
      registerFormData.append("username", this.registerForm.username)
      registerFormData.append("password", this.registerForm.confirmPassword)
      registerFormData.append("roleId", this.registerForm.roleId)
      requestRegister(registerFormData).then(res => {
        if(res.data.code==="00000"){
          this.$message.success("注册成功");
          this.$router.push("/login")
        }
      })
    }
  }
}
</script>

<style scoped>
@import "~assets/css/register-view.css";
</style>
